.eidt-page-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.eidt-page-wrapper .save-btn {
  position: absolute;
  right: 20px;
  top: 5px;
}
.edit-page {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.edit-page .ant-divider-horizontal {
  margin: 0;
}
.edit-page .edit-page-content {
  height: calc(100% - 24px - 1px);
  width: 100%;
  margin-top: 24px !important;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}
@media only screen and (max-width: 675px) and (min-width: 414px) {
  .edit-page .edit-page-content .edit-page-core {
    display: flex;
    justify-content: center;
  }
}
.edit-page .edit-page-content .edit-page-core {
  width: 100%;
  padding-left: 20px;
}
.edit-page .edit-page-content .edit-page-core .ant-spin-blur {
  opacity: 1;
  height: 100%;
}
.edit-page .edit-page-content .edit-page-core .ant-spin-nested-loading {
  height: 100%;
}
.edit-page .edit-page-content .edit-page-core .ant-spin-container {
  height: 100%;
}
